<template>
  <div class="center">
    <processHead :paths="['首页','功能建设中']"></processHead>
    <div class="content">
      <div>
      <h1>功能建设中</h1>
        <p>非常抱歉,该功能暂时无法使用，开发人员正在努力开发中，敬请期待！</p>
        <button @click="tohome">{{num}}秒后返回首页</button>
      </div>
      <img src="@/assets/error/error.png" alt="">
    </div>
  </div>
</template>

<script>
  import processHead from '@/components/processHead';

  export default {
    name: 'error',
    mounted(){
      this.timer = setInterval(function () {
        this.num--;
        if(this.num<=0){
          this.tohome();
        }
      }.bind(this),1000);
    },
    destroyed() {
      clearInterval(this.timer);
    },
    data() {
      return {
        num:10
      }
    },
    methods:{
      tohome(){
        this.$router.push("/");
      }
    },
    components: {processHead}
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .content {
    margin: 0 auto;
    margin-top: 30px;
    padding-top: 100px;
    width: 987px;
    height: 520px;
    border-radius: 15px;
    background-color: rgba(0, 146, 255, 0.08);
    box-shadow: 5px 5px 12px rgba(1, 18, 51, .18);
  }
  .content div{
    display: inline-block;
    width: 400px;
    height: 300px;
    margin: 40px;
    margin-left: 100px;
  }
  .content h1{
    font-size: 40px;
  }
  .content p {
    font-size: 18px;
    padding: 10px 0;
    color: #137ccc;
  }
  button {
    font-size: 16px;
    width: 180px;
    height: 63px;
    color: #FFFFFF;
    border: none;
    background: url("../assets/process/button.png") no-repeat;
  }
</style>



